import { Component, OnInit, Input } from '@angular/core';
import { ModalController, Platform } from '@ionic/angular';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { BiografiaService } from '../../../providers/Api/Biografia.service';
import { InicioService } from '../../../providers/Almacenamiento/inicio.service';
import { LoadingService } from '../../../providers/Utilidades/LoadingService.service';
import { ToastServices } from 'src/app/providers/Utilidades/ToastService.service';
import { transition, style, animate, trigger } from '@angular/animations';
import { StatusBar } from '@ionic-native/status-bar/ngx';

@Component({
  selector: 'app-modal',
  templateUrl: './modal.page.html',
  styleUrls: ['./modal.page.scss'],
  animations: [
    trigger('entrada', [
      transition(':enter', [
        style({
          opacity: 0,
          transform: 'scale(0)'
        }),
        animate('250ms', style({
          opacity: 1,
          transform: 'scale(1)'
        }))
      ]),

      transition(':leave', [
        style({
          opacity: 1,
          transform: 'scale(1)'
        }),
        animate('250ms', style({
          opacity: 0,
          transform: 'scale(0)'
        }))
      ])
    ])
  ]
})
export class ModalPage implements OnInit {

  @Input() imagen;
  @Input() type;


  slideOpt = {
    zoom: {
      maxRatio: 3,
      minRatio: 1,
      toggle: true
    }
  };

  public cancelSuscribe: any;

  msgEstado = '';
  nuevoServicio = false;
  textoServicio = '';
  listaServicios = [];

  data = {
    contenido: null,
    servicios: []
  };

  dataNegocio = {
    direccion: '',
    pagina: '',
    desde: '',
    hasta: '',
    telefono: '',
    ciudad: '',
    pais: '',
    historia: '',
    de: '',
    a: ''
  }

  dataVehiculo = {
    tipo: '',
    Marca: '',
    Modelo: '',
    Nombre: ''
  };

  role = '';

  serviciosTemp = [];
  load = false;
  
  constructor(private modalCtrl: ModalController,
              private biografia: BiografiaService,
              private almacenamiento: InicioService,
              private loading: LoadingService,
              private toast: ToastServices,
              private statusbar: StatusBar, 
              private plt: Platform) { }

  ngOnInit() {

  }

  ionViewDidEnter() {
    console.log(this.type);
    if (this.type === 'mediap' || this.type === 'mediapor') {
      try {
        this.load = true;
        this.plt.ready().then(() => {
          this.statusbar.hide();
        });
      } catch (error) {
        
      }
    } else if (this.type === 'form') {

      this.almacenamiento.obtenerToken().then((info: any) => {
        this.msgEstado = 'Información de mi negocio'
        console.log(info)
        this.role = info.role;
        this.biografia.getBiografia(info.token, 'none').subscribe((data: any) => {
          console.log('Data', data);
          if ( data.exe ) {
            this.data = data.response.length > 0 ? data.response[0] : this.data;
            this.dataNegocio = data.response.length > 0 ? this.data.contenido : this.dataNegocio;
          } else {
            this.toast.alertApplication('No se puede tarer la información', true)
          }
        }, (err) => console.log('error'));
      });

     

    } else if (this.type === 'vehiculo') {

      this.almacenamiento.obtenerToken().then((info: any) => {
        this.role = info.role;
        console.log(info)
        this.biografia.getBiografia(info.token, 'none').subscribe((data: any) => {
          console.log('Data', data);
          if ( data.exe ) {
            this.data = data.response.length > 0 ? data.response[0] : this.data;
            this.dataVehiculo = data.response.length > 0 ? this.data.contenido : this.dataVehiculo;

            console.log()
          } else {
            this.toast.alertApplication('No se puede traer la información', true)
          }
        });
      });

      this.msgEstado = 'Información del Vehículo';

    } else if (this.type === 'servis') {

      this.almacenamiento.obtenerToken().then((info: any) => {
        this.msgEstado = 'Servicios que ofrezco en mi negocio'
        this.role = info.role;
        this.biografia.getBiografia(info.token, 'none').subscribe((data: any) => {
          console.log('Data', data);

          if ( data.exe ) {
            this.data = data.response.length > 0 ? data.response[0] : this.data;
            this.serviciosTemp = data.response.length > 0 ? data.response[0].servicios : this.data.servicios;
            this.dataNegocio = data.response.length > 0 ? this.data.contenido : this.dataNegocio;
          } else {
            this.toast.alertApplication('No se puede tarer la información', true)
          }
        });
      });

      

    }
  }

  horario(event , tipo) {
    if (tipo === 'desde') {
      this.dataNegocio.desde = event.detail.value;
    } else {
      this.dataNegocio.hasta = event.detail.value;
    }

  }

  dias( event, tipo) {
    if (tipo === 'de') {
      this.dataNegocio.de = event.detail.value;
    } else {
      this.dataNegocio.a = event.detail.value;
    }
  }

  tipoVehiculo(event) {
    this.dataVehiculo.tipo = event.detail.value;
  }

  cambiarServicio( event, idx) {
    this.serviciosTemp[idx] = event.detail.value;
    console.log(event)
  }


  eliminar(idx) {
    this.data.servicios.splice(idx, 1);
  }

  salirModal() {
    this.statusbar.show();
    if (typeof this.cancelSuscribe !== 'undefined') {
      console.log('Cerramos el suscribe');
      this.cancelSuscribe.unsubscribe();
    }
    this.modalCtrl.dismiss({
      data: this.data
    });
  }

  
  habilitarTrabajo() {
    this.nuevoServicio = true;
}
async crearTrabajoChat() {
  
  if (this.nuevoServicio && this.textoServicio !== '') {
      this.data.servicios.unshift(this.textoServicio);
      this.textoServicio = '';
  }
}

cancelarTrabajo() {
  this.nuevoServicio = false;
}

  guardarCambios() {
      this.almacenamiento.obtenerToken().then((info: any) => {
       if ( info.role === 'TALLER_ROLE' && (this.type === 'form' || this.type === 'servis')) {
         this.data.contenido = this.dataNegocio;
       } else if ( info.role === 'USER_ROLE' && this.type === 'vehiculo') {
        this.data.contenido = this.dataVehiculo;
      }
       console.log(this.data);
       this.biografia.postBiografia(info.token, this.data).subscribe((data: any) => {
        console.log(data);
        if (data.exe) {
          this.toast.alertApplication('Se actualizó tu perfil!', true)
          this.modalCtrl.dismiss({
            servicios: this.data.servicios,
            contenido: this.data.contenido
          })
        } else {
          this.toast.alertApplication('No se pudo guardar los cambios, intentalo más tarde', true)
        }
      })
      })
  }

}
